import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Actividad } from '../../Actividad/Actividad';
import { Alumno } from '../../estudiante/alumno'
import { SelectionModel } from '@angular/cdk/collections';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';

export interface PeriodicElement {
  name: string;
  position: number;
  weight: number;
  symbol: string;
}

const ELEMENT_DATA: PeriodicElement[] = [
  { position: 1, name: 'Hydrogen', weight: 1.0079, symbol: 'H' },
  { position: 2, name: 'Helium', weight: 4.0026, symbol: 'He' },
  { position: 3, name: 'Lithium', weight: 6.941, symbol: 'Li' },
  { position: 4, name: 'Beryllium', weight: 9.0122, symbol: 'Be' },
  { position: 5, name: 'Boron', weight: 10.811, symbol: 'B' },
  { position: 6, name: 'Carbon', weight: 12.0107, symbol: 'C' },
  { position: 7, name: 'Nitrogen', weight: 14.0067, symbol: 'N' },
  { position: 8, name: 'Oxygen', weight: 15.9994, symbol: 'O' },
  { position: 9, name: 'Fluorine', weight: 18.9984, symbol: 'F' },
  { position: 10, name: 'Neon', weight: 20.1797, symbol: 'Ne' },
];

@Component({
  selector: 'app-crear-actividad',
  templateUrl: './crear-actividad.component.html',
  styleUrls: ['./crear-actividad.component.css']
})
export class CrearActividadComponent implements OnInit {

  @Input() estudiantes: Alumno[];

  //Prueba de una tabla con filtro y paginacion
  displayedColumns = ['select', 'nombre', 'apellidos', 'clase'];
  dataSource: MatTableDataSource<Alumno>;
  selection = new SelectionModel<Alumno>(true, []);

  @ViewChild(MatPaginator, null) paginator: MatPaginator;
  @ViewChild(MatSort, null) sort: MatSort;

  datosPrimarios: FormGroup;
  seleccionAlumno: FormGroup;

  actividadCreate = {
    nombre: '',
    profesor: '',
    alumnos: {},
  }

  estudiante = {
    nombre:'',
    apellidos:'',
    clase:'',
    posicionEnClase:''
    }

  constructor(private _formBuilder: FormBuilder) {

  }

  ngOnInit() {
    this.datosPrimarios = this._formBuilder.group({
      fromControlNombreActividad: true,//['', Validators.required],
      fromControlNombreProfesor: true//['', Validators.required]
    });
  }
  ngOnChanges() {
    this.dataSource = new MatTableDataSource(this.estudiantes);
  }
  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
  }

  checkboxLabel(row?: Alumno): string {
    if (!row) {
      return `${this.isAllSelected() ? 'select' : 'deselect'} all`;
    }
    return `${this.selection.isSelected(row) ? 'deselect' : 'select'}`;
  }

  verSeleccionados() {
    //console.log(this.comprobarSeleccionados())
    this.crearActividad();
  }

  comprobarSeleccionados() {
    return(this.selection.selected.length > 0)
  }

  crearActividad(){
    this.actividadCreate.alumnos=this.selection.selected;
    console.log(this.actividadCreate);
  }

}